"use strict";
const _ = require('lodash');
const minimist = require('minimist');
const LoaderFactory_1 = require('./LoaderFactory');
const development_core_1 = require('development-core');
const Context_1 = require('./Context');
const chalk = require('chalk');
class Development {
    /**
     * Creates an instance of Development.
     *
     * @param {string} dirname
     * @param {DevelopConfig} config
     *
     * @memberOf Development
     */
    constructor(dirname, config) {
        this.dirname = dirname;
        this.config = config;
    }
    /**
     * create development tool.
     *
     * @static
     * @param {Gulp} gulp
     * @param {string} dirname
     * @param {(DevelopConfig | Array<ITaskOption | IAssertOption | IDynamicTaskOption>)} setting
     * @param {any} [runWay=RunWay.sequence]
     * @returns {Development}
     *
     * @memberOf Development
     */
    static create(gulp, dirname, setting, runWay = development_core_1.RunWay.sequence, factory) {
        let option = _.isArray(setting) ? { tasks: setting, runWay: runWay } : setting;
        if (!_.isUndefined(option.runWay)) {
            option.runWay = runWay;
        }
        option.contextFactory = factory || ((cfg, parent) => {
            let ctx = new Context_1.Context(cfg, parent);
            if (parent && parent['add']) {
                parent.add(ctx);
            }
            return ctx;
        });
        let devtool = new Development(dirname, option);
        option.setupTask = option.setupTask || 'build';
        gulp.task(option.setupTask, (callback) => {
            var options = minimist(process.argv.slice(2), {
                string: 'env',
                default: { env: process.env.NODE_ENV || 'development' }
            });
            return devtool.run(gulp, options);
        });
        gulp.task('default', () => {
            gulp.start(option.setupTask);
        });
        return devtool;
    }
    /**
     * run task.
     *
     * @param {Gulp} gulp
     * @param {IEnvOption} env
     * @returns {Promise<any>}
     *
     * @memberOf Development
     */
    run(gulp, env) {
        if (!env.root) {
            env.root = this.dirname;
        }
        if (env.help) {
            console.log(chalk.grey('... main help  ...'));
            this.printHelp(env.help);
        }
        let gbctx = this.getContext(env);
        return this.loadTasks(gulp, this.config.tasks, gbctx)
            .then(tseq => {
            // console.log(chalk.grey('run sequenec tasks:'), tseq);
            if (this.config.runWay === development_core_1.RunWay.parallel) {
                return development_core_1.runSequence(gulp, [development_core_1.flattenSequence(gulp, tseq, gbctx)]);
            }
            else {
                return development_core_1.runSequence(gulp, tseq);
            }
        })
            .catch(err => {
            console.error(err);
            process.exit(1);
        });
    }
    getContext(env) {
        if (!this.globalctx || this.globalctx.env !== env) {
            let option = this.config.option || {};
            this.globalctx = this.config.contextFactory({
                env: env,
                option: option
            });
        }
        return this.globalctx;
    }
    loadTasks(gulp, tasks, parent) {
        return Promise.all(_.map(_.isArray(tasks) ? tasks : [tasks], optask => {
            optask.dist = optask.dist || 'dist';
            // console.log(chalk.grey('begin load task via loader:'), optask.loader);
            let loader = this.createLoader(optask, parent.env);
            return loader.loadContext(parent.env)
                .then(ctx => {
                console.log(chalk.green('task context loaded.'));
                if (ctx.env.help) {
                    if (ctx.printHelp) {
                        console.log(chalk.grey('...development default help...'));
                        ctx.printHelp(_.isString(ctx.env.help) ? ctx.env.help : '');
                    }
                    return [];
                }
                else {
                    return Promise.all([
                        loader.load(ctx),
                        this.loadAssertTasks(gulp, ctx),
                        this.loadSubTask(gulp, ctx)
                    ])
                        .then(tks => {
                        console.log(chalk.green('tasks loaded.'));
                        return this.setup(gulp, ctx, tks[0], tks[1], tks[2]);
                    });
                }
            });
        }))
            .then(tsq => {
            let rst = [];
            _.each(tsq, t => {
                let tk = development_core_1.zipSequence(gulp, t, parent);
                if (tk) {
                    rst.push(tk);
                }
            });
            return rst;
        });
    }
    setup(gulp, ctx, tasks, assertsTask, subGroupTask) {
        return Promise.resolve(development_core_1.toSequence(gulp, tasks, ctx))
            .then(tsqs => {
            // if (_.isFunction(context.option['runTasks'])) {
            //     return context.option['runTasks'](context.oper, tsqs, subGroupTask, assertsTask);
            // } else if (_.isArray(context.option['runTasks'])) {
            //     tsqs = context.option['runTasks'];
            // } else 
            if (ctx.runTasks) {
                return ctx.runTasks(tsqs, assertsTask, subGroupTask);
            }
            // console.log(assertsTask);
            ctx.addToSequence(tsqs, assertsTask);
            ctx.addToSequence(tsqs, subGroupTask);
            return tsqs;
        });
    }
    /**
     * load sub tasks as group task.
     *
     * @protected
     * @param {Gulp} gulp
     * @param {IContext} ctx
     * @returns {Promise<ITaskInfo>}
     *
     * @memberOf Development
     */
    loadSubTask(gulp, ctx) {
        if (ctx.option['tasks']) {
            let optask = ctx.option;
            _.each(_.isArray(optask.tasks) ? optask.tasks : [optask.tasks], subopt => {
                subopt.name = ctx.subTaskName(subopt.name);
                subopt.src = subopt.src || optask.src;
                subopt.dist = subopt.dist || optask.dist;
            });
            return this.loadTasks(gulp, optask.tasks, ctx)
                .then(subseq => {
                let taskname;
                if (optask.subTaskRunWay === development_core_1.RunWay.parallel) {
                    taskname = [development_core_1.flattenSequence(gulp, subseq, ctx, (name, runway) => ctx.subTaskName(name, (runway === development_core_1.RunWay.sequence ? '-subs' : '-subp')))];
                }
                else {
                    taskname = development_core_1.zipSequence(gulp, subseq, ctx, (name, runway) => ctx.subTaskName(name, (runway === development_core_1.RunWay.sequence ? '-subs' : '-subp')));
                }
                if (taskname) {
                    return {
                        order: optask.subTaskOrder,
                        taskName: taskname
                    };
                }
                else {
                    return null;
                }
            });
        }
        else {
            return Promise.resolve(null);
        }
    }
    /**
     * load asserts tasks.
     *
     * @protected
     * @param {Gulp} gulp
     * @param {ITaskContext} ctx
     * @returns {Promise<Src>}
     *
     * @memberOf Development
     */
    loadAssertTasks(gulp, ctx) {
        let optask = ctx.option;
        if (optask.asserts) {
            let tasks = [];
            _.each(_.keys(optask.asserts), name => {
                let op;
                let sr = optask.asserts[name];
                if (_.isString(sr)) {
                    op = { src: sr, loader: [{ name: name, pipes: [], watch: true }] };
                }
                else if (_.isNumber(sr)) {
                    // watch with Operation.autoWatch.
                    op = { loader: [{ oper: sr, name: name, pipes: [] }] };
                }
                else if (_.isFunction(sr)) {
                    op = { loader: sr };
                }
                else if (_.isArray(sr)) {
                    if (sr.length > 0) {
                        if (_.isString(_.first(sr))) {
                            op = { src: sr, loader: [{ name: name, pipes: [], watch: true }] };
                        }
                        else {
                            op = { loader: sr, watch: true };
                        }
                    }
                }
                else {
                    op = sr;
                }
                if (_.isNull(op) || _.isUndefined(op)) {
                    return;
                }
                op.name = op.name || ctx.subTaskName(name);
                op.src = op.src || (ctx.getSrc({ oper: development_core_1.Operation.build }) + '/**/*.' + name);
                op.dist = op.dist || ctx.getDist({ oper: development_core_1.Operation.build });
                tasks.push(op);
            });
            return Promise.all(_.map(tasks, task => {
                return this.loadTasks(gulp, task, ctx)
                    .then(sq => {
                    return {
                        task: task,
                        sq: sq
                    };
                });
            }))
                .then(tseq => {
                // asserts tasks run mutil.
                let assertSeq = _.map(tseq, t => {
                    return development_core_1.zipSequence(gulp, t.sq, ctx, (name, runway) => ctx.subTaskName(t.task.name, runway === development_core_1.RunWay.sequence ? '-asserts' : '-assertp')); // ctx.subTaskName(name + (runway === RunWay.sequence ? '-assert-seq' : '-assert-par')));
                });
                let taskname;
                if (optask.assertsRunWay === development_core_1.RunWay.sequence) {
                    taskname = assertSeq;
                }
                else {
                    taskname = development_core_1.zipSequence(gulp, [assertSeq], ctx, (name, runway) => name + (runway === development_core_1.RunWay.sequence ? '-asserts' : '-assertp'));
                }
                return {
                    order: optask.assertsOrder,
                    taskName: taskname
                };
            });
        }
        else {
            return Promise.resolve(null);
        }
    }
    createLoader(option, env) {
        let loader = null;
        if (!_.isFunction(this.config.loaderFactory)) {
            let factory = new LoaderFactory_1.LoaderFactory();
            this.config.loaderFactory = (opt) => {
                return factory.create(opt, env, this.config.contextFactory);
            };
        }
        loader = this.config.loaderFactory(option, env);
        return loader;
    }
    printHelp(help) {
        if (help === 'en') {
            console.log(`
                /**
                 * gulp [build] [--env production|development] [--context name] [--root path] [--watch] [--test] [--serve] [--release] [--task taskname]
                 * @params
                 *  --env  development or production;
                 *  --context app setting
                 *  --root path, set relative path of the development tool root.
                 *  --watch  watch src file change or not. if changed will auto update to node service. 
                 *  --release release web app or not. if [--env production], default to release. 
                 *  --test  need auto load test file to node service.
                 *  --deploy run deploy tasks to deploy project.  
                 *  --serve start node web service or not.
                 *  --task taskname  spruce task taskname
                 **/`);
        }
        else {
            console.log(`
                /**
                 * gulp [build] [--env production|development] [--context name] [--root path] [--watch] [--test] [--serve] [--release] [--task taskname]
                 * @params
                 *  --env 发布环境 默认开发环境development;
                 *  --context 设置配置文件;
                 *  --root path, 设置编译环境相对路径
                 *  --watch  是否需要动态监听文件变化
                 *  --release 是否release编译, [--env production] 默认release 
                 *  --test  启动自动化测试
                 *  --deploy 运行加载deploy tasks, 编译发布项目。  
                 *  --serve  是否在开发模式下 开启node web服务
                 *  --task taskname  运行单独任务taskname
                 **/`);
        }
    }
}
exports.Development = Development;
